<template>
  <div class="container">
    <div class="top"></div>
    <div class="top_2"> <br>202108-02-02<br></div>
      <div class="wdbox"> 
        <div class="wdcon" ref="wdcon" id="wdcon">
          <div class="box">
            <div class="left"><img src="../../static/images/wdys.jpg"></div>
            <div class="right">
              <div class="con"><span class="sjx"></span></div>
            </div>
            <div class="clear"></div>
          </div>
          <div class="box">
            <div class="left"><img src="../../static/images/wdys.jpg"></div>
            <div class="right">
              <div class="con"><span class="sjx"></span></div>
            </div>
            <div class="clear"></div>
          </div>
          <div class="box">
            <div class="left"><img src="../../static/images/wdys.jpg"></div>
            <div class="right">
              <div class="con"><span class="sjx"></span></div>
            </div>
            <div class="clear"></div>
          </div>
        </div>
      </div>
      <div style="height: 4rem;"></div>
      <div :class="{'wdfixbom':true,'disabled':disabled}" ref="wdfixbom" id="wdfixbom" @click="addEle($event)">
          <div class="btnbox w50"><button>20</button></div>
          <div class="btnbox w50"><button>2030</button></div>
          <div class="btnbox w50"><button>3045</button></div>
          <div class="btnbox w50"><button>45</button></div>
      </div>
      <div style="clear:both;"></div>
    </div>
</template>

<script>
  import {txchatInit} from '../assets/js/eliao.js';
  import {txchat} from '../assets/js/eliao.js';
  
  export default{
		data(){
			return{
        loadbtnstr:['<div class="btnbox w100"><button></button></div>'],
        btnarr:[
          `<div class="btnbox w50"><button></button></div><div class="btnbox w50"><button></button></div><div class="btnbox w50"><button></button></div><div class="btnbox w50"><button>M</button></div><div class="btnbox w50"><button></button></div><div class="btnbox w50"><button></button></div>`,
          '<div class="btnbox w50"><button>1</button></div><div class="btnbox w50"><button>13</button></div><div class="btnbox w50"><button>35</button></div><div class="btnbox w50"><button>5</button></div>',
          '<div class="btnbox w30"><button></button></div><div class="btnbox w30"><button></button></div><div class="btnbox w30"><button></button></div>',
          '<div class="btnbox w50"><button></button></div><div class="btnbox w50"><button></button></div>',
          '<div class="btnbox w30"><button></button></div><div class="btnbox w30"><button></button></div><div class="btnbox w30"><button></button></div>','<div class="btnbox w100" id="gwx"><button class="wx"></button></div>'
        ],
        waqs:[
          " ",
          " ",
          " ",
          "MM ",
          " "
        ],
        warr:[
          "",
          "",
          "",
          "",
          "",
          " "
        ],
        numb:0,
        timer:"",
        disabled:false
      }
		},
    methods:{
      appendbtn:function(index){
        this.$refs.wdfixbom.innerHTML="";
        this.$refs.wdfixbom.innerHTML=this.btnarr[index];
      },
      loadbtn:function(index){
        this.$refs.wdfixbom.innerHTML="";
        this.$refs.wdfixbom.innerHTML=this.loadbtnstr[index];
      },
      apphzlt:function(text){
        var str = '<div class="hzbox">';
        str += '<div class="left">';
        str += '<img src="../../static/images/timg.jpg">';
        str += "</div>";
        str += '<div class="right">';
        str += '<div class="con"><span class="sjx"></span>' + text + "</div>";
        str += "</div>";
        str += '<div class="clear"></div>';
        str += "</div>";
        this.$refs.wdcon.innerHTML+=str;    
        var height_one = document.body.scrollHeight;
        var height_two = window.innerHeight;
        var height = height_one - height_two;
        $("html").animate({scrollTop: height},400);
      },
      appyslt:function(text){
        var str = '<div class="box">';
        str += '<div class="left">';
        str += '<img src="../../static/images/wdys.jpg">';
        str += "</div>";
        str += '<div class="right">';
        str += '<div class="con"><span class="sjx"></span>' + text + "</div>";
        str += "</div>";
        str += '<div class="clear"></div>';
        str += "</div>";
        this.$refs.wdcon.innerHTML+=str;
        var height0 = document.body.scrollHeight;
        var height1 = window.innerHeight;
        var height = height0 - height1;
        $("html").animate({scrollTop: height},400);
      },
      addEle(e){
        if(this.numb==6){
          txchat(24962,81707,53669);
          return false;
        }
        this.apphzlt(e.target.innerText)
        let target=e.target.parentNode;
        let nodeList = e.currentTarget.children;
        let targetIndex=this.tabIndex(target,nodeList);
        this.loadbtn(0);
        this.disabled=true;
        this.timer=setTimeout(()=>{
          this.appyslt(this.warr[this.numb]);
          this.appendbtn(this.numb);
          this.disabled=false;
          if(this.numb==1&&targetIndex<5){
            this.appyslt(this.waqs[targetIndex]);
          }               
          this.numb++;
        }, 1000);
      },
      tabIndex(target,nodeList){
        for(let i=0;i<nodeList.length;i++){
          if(target===nodeList[i]){
            return i;
          }
        }
      }
    },
    mounted(){
      this.$nextTick(function(){
        txchatInit(24962,81707);
      })
    }    
  }
</script>

<style>
  @import "../assets/css/wendael.css";
</style>





